import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			video:{},
			muted:false
		}
	},
	methods: {
		downvideo() {

			uni.showLoading({
				title:"下载中"
			})
			
			uni.downloadFile({
				url:this.video.video,
				success:(result) => {
					uni.saveVideoToPhotosAlbum({
						filePath:result.tempFilePath,
						success: () => {
							uni.showToast({
								title:"保存成功"
							})
						},
						complete: () => {
							uni.hideLoading()
						}						
					})
				}
			})
			/*
			uni.downloadFile({
				url:this.video.img,
				success:(result) => {
					uni.saveImageToPhotosAlbum({
						filePath:result.tempFilePath,
						success: () => {
							uni.showToast({
								title:"保存成功"
							})
						},
						complete: () => {
							uni.hideLoading()
						}							
					})
				}
			})*/
			
		},
		totoggle() {
			this.muted = !this.muted;
		}
	},
	onLoad() {
		this.video = getApp().globalData.video;
		//console.log(this.video)
	},
	components: {
		uniIcons
	}
}
